<template>
  <div class="member">
    <div class="member-top">
      <div class="member-item" v-for="(item, i) in list" :key="i">
        <img class="img" :src="item.avatar" alt="" @click="handOpenChat(item)">
        <span>{{item.nickName}}</span>
      </div>
      <div class="member-item">
        <!-- 展开 -->
      </div>
    </div>
    <div class="member-mid">
      <mt-cell title="群聊名称" :to="`/editName/${item.chatName}`" is-link value="修改"></mt-cell>
      <mt-cell title="群公告" to="/groupNotice" is-link value="未设置"></mt-cell>
      <mt-cell title="群昵称" to="" is-link value="木木姐姐"></mt-cell>
      <mt-cell title="查找聊天内容" to="" is-link value=""></mt-cell>
    </div>
    <div class="member-footer">
      <div class="my-border name">清空聊天记录</div>
      <div class="my-border name">删除并退出</div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'Member',
  data () {
    const user = localStorage.getItem('user')
    const item = localStorage.getItem('chatItem')
    return {
      user: (user && JSON.parse(user)) || {},
      item: (item && JSON.parse(item)) || {},
      list: []
    }
  },
  created () {
    this.$socket.getGroupMembers(this.item.chatId, this.user.id, (res) => {
      this.list = res.members
    })
  },
  methods: {
    handOpenChat (member) {
      this.$router.push('/friendChat')
      localStorage.setItem('friendChatItem', JSON.stringify(member))
    }
  }
}
</script>
<style lang="less">
  .member {
    .member-mid {
      margin-top: 10px;
    }
    .member-footer {
      margin-top: 20px;
      background: #fff;
      .name {
        height: 70px;
        color: #FF6464;
        font-size: 15px;
        text-align: center;
        line-height: 70px;
      }
    }
    .icon-ico {
      font-size: 20px;
    }
    .member-top {
      min-height: 100px;
      padding-bottom: 14px;
      background: #fff;
      display: flex;
      flex-wrap: wrap;
      .add-box {
        border: 1px dashed #D8D8D8;
        width: 50px;
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        .iconfont {
          color: #D8D8D8;
          font-size: 20px;
        }
      }
      .member-item {
        width: 20%;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-direction: column;
        padding-top: 14px;
        span {
          font-size: 12px;
          color: #748EAC;
          margin-top: 5px;
        }
        .img {
          width: 50px;
          height: 50px;
          border-radius: 50%;
        }
      }
    }
  }
</style>
